.App {
  text-align: center;
}

.company-logo {
  width: 150px;
  height: auto;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
}

footer {
  text-align: center;
  margin-top: 20px;
  background-color: #f8f8f8;
  padding: 20px 0;
}

footer ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

footer ul li {
  margin: 0 10px;
}

footer ul li a {
  text-decoration: none;
  color: #000;
}

footer p {
  font-size: 0.9rem;
  color: #777;
}

